*{
  margin: 0;
  padding: 0;
}
body{
  background-image: url("TO\ DO\ PIC.jpg");
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.center{
  padding: 2rem;
    border-radius: 1rem;
    min-height: 45vh;
    width: 40vh;
  background-color: rgb(169, 213, 209);
}

h1{
  font-size: 3rem;
  align-items: center;
}
input{
  width: 100%;
    border: none;
    outline: none;
    border-radius: 0.75rem;
    border: 1px solid lightgray;
    font-size: 1rem;
    height: 3rem;
    padding-left: 0.5rem;
}
button{
  /* height: 3rem; */
  width: 70px;
  margin-left: 100px;
  margin-top: 20px;
  padding: 1rem;
  border-radius: 0.75rem;
  background-color: #9273aa;
  color: #fff;
  font-size: 1.15rem;
  cursor: pointer;
}
.list{
  font-size: 1.5rem;
  cursor: pointer;
}
